body {
  margin: 0;
  padding: 0;
  background: url("assets/background.png") 100% 100%;
  color: #FFF;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
}

.header div {
  margin: 0 20px;
}

.header {
  font-size: 24px;
  margin: 0 0 68px 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #4682B4;
  color: #FFF;
  border: 3px solid #000;
  width: 99%;
  height: 60px;
}

.content {
  display: flex;
}

.moskva {
  margin: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
  max-width: 100%;
}

.header_moskva {
  width: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: bold;
  margin: 0 0 30px 0;
}

.sadovod {
  margin: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
  width: 100%;
}

.header_sadovod {
  width: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: bold;
  margin: 0 0 30px 0;
}

.vorota {
  margin: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
  width: 100%;
}

.header_vorota {
  width: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: bold;
  margin: 0 0 30px 0;
}

.img {
  width: 300px;
  height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 0 20px 0;
  border: 1px solid #000;
}

.button {
  width: 150px;
  height: 60px;
  font-size: 16px;
  background-color: #FFF;
  color: black;
  cursor: pointer;
  border-radius: 10px;
}

.text {
  margin: 0 0 50px 0;
}

.contacts {
  margin: 150px 0 170px 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
}

.phone {
  margin: 0 0 25px 0;
}

.footer {
  position: absolute;
  left: 0;
  bottom: -400px;
  background-color: #000;
  color: #FFF;
  display: flex;
  font-size: 21px;
  justify-content: center;
  align-items: center;
  height: 100px;
  padding: 25px;
  width: 96.67%;
}

.img-before {
  width: 20px;
  height: 20px;
  margin: 5px 5px 0 0;
}

.hr {
  width: 5px;
  background-color: #000;
  margin: 0 0 0 -150px;
}

@media (max-width: 1280px) {
  .header {
    /*margin: 0 0 50px 3%;*/
    font-size: 14px;
  }

  .content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
  }

  .moskva {
    margin: 0 0 50px 0;
  }

  .sadovod {
    margin: 0 0 50px 0;
  }

  .vorota {
    margin: 0 0 50px 0;
  }

  .header_vorota {
    width: 200px;
  }

  .footer {
    font-size: 14px;
    position: relative;
  }

  .dop-content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
  }
}

@media (max-width: 780px) {
  .header {
    margin: 0 0 50px 3%;
    font-size: 14px;
  }
}

@media (max-width: 580px) {
  .header {
    margin: 0 0 50px 3%;
    font-size: 14px;
    width: 95%;
  }
}

@media (max-width: 380px) {
  .header {
    margin: 0 0 50px 3%;
    font-size: 14px;
  }
}

@media (min-width: 2000px) {
  .footer {
    position: absolute;
    bottom: -400px;
    left: 0;
    width: 99%;
  }
}

.dop {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
}

.dop-header {
  margin: 0 0 50px 0;
}

.dop-content {
  display: flex;
  justify-content: center;
  align-items: center;
}

.block {
  margin: 20px;
}

